* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  padding: 20px;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 18px;
}

.country-info ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.country-info li {
  font-size: 18px;
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  flex-basis: calc(100% - 200px);
}
